
.home-page {
    display: block;
    max-width: 600px;
    margin: 0 auto;
    padding: 40px;
    background-color: #f5f5f5;
    border-radius: 4px;
    text-align: center;
    box-shadow: 0px 0px 40px -10px rgba(0, 0, 0, 0.25);
  }

  .server-side {
    position: fixed;
    display: flex;
    margin-left: 180px;
    max-height: 250px;
    background-color: #acacac;
    border-radius: 4px;
    box-shadow: 0px 0px 40px -10px rgba(0, 0, 0, 0.25);
    z-index: 150;
  }
  
  
  h1 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  p {
    font-size: 16px;
    margin-bottom: 10px;
  }
  
  .robotImg{
    max-width: 75%;
  }